import axios from "axios";
export default {
	namespaced: true,
	state: {
		refBrandItems: [
			{
				id: 1,
				icon: "icon1_1.svg",
				link: "apple.com",
			},
			{
				id: 2,
				icon: "icon1_2.svg",
				link: "apple.com",
			},
			{
				id: 3,
				icon: "icon2_1.svg",
				link: "apple.com",
			},
			{
				id: 4,
				icon: "icon2_2.svg",
				link: "apple.com",
			},
			{
				id: 5,
				icon: "icon3_1.svg",
				link: "apple.com",
			},
			{
				id: 6,
				icon: "icon3_2.svg",
				link: "apple.com",
				width: "43%",
			},
			{
				id: 7,
				icon: "icon4_1.svg",
				link: "apple.com",
			},
			{
				id: 8,
				icon: "icon4_2.svg",
				link: "apple.com",
			},
			{
				id: 9,
				icon: "icon5_1.svg",
				link: "apple.com",
			},
			{
				id: 10,
				icon: "icon5_2.svg",
				link: "apple.com",
				width: "46%",
			},
		],
	},
	mutations: {
		setRefBrandItems(state, refBrandItems) {
			state.refBrandItems = refBrandItems;
		},
	},
	getters: {
		_brandItems(state) {
			return state.refBrandItems;
		},
	},
	actions: {
		async getRefBrandItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_BRAND_REF;
			try {
				const response = await axios.get(apiUrl); // axios doğrudan kullanılıyor
				commit("setRefBrandItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
