<template>
	<div class="menuColumn">
		<div v-for="item in footerMenuItems" :key="item.id">
			<div>{{ item.text }}</div>
		</div>
	</div>
</template>
<style scoped>
	.menuColumn {
		display: flex;
		flex-direction: row;
		color: #ed1651;
		font-size: 18px;
		column-gap: 85px;
		margin-top: 85px;
		margin-left: 105px;
		cursor: pointer;
	}
</style>
<script>
	import { mapGetters } from "vuex";
	export default {
		created() {
			this.$store.dispatch("footerMenuItem/getFooterMenuItems");
		},
		computed: {
			...mapGetters({
				footerMenuItems: "footerMenuItem/_footerMenuItems",
			}),
		},
	};
</script>
