export default {
	namespaced: true,
	state: {
		loading: true,
		loadingPercentage: 0,
	},
	mutations: {
		SET_LOADING(state, loading) {
			state.loading = loading;
		},
		SET_LOADING_PERCENTAGE(state, percentage) {
			state.loadingPercentage = percentage;
		},
	},
	getters: {
		_loading(state) {
			return state.loading;
		},
		_loadingPercentage(state) {
			return state.loadingPercentage;
		},
	},
	actions: {
		async simulateLoading(context) {
			const increment = 1;
			const stepDuration = 50;
			const totalSteps = 100;

			context.commit("SET_LOADING", true);

			const interval = setInterval(() => {
				if (context.state.loadingPercentage < 100) {
					context.commit(
						"SET_LOADING_PERCENTAGE",
						context.state.loadingPercentage + increment
					);
				} else {
					clearInterval(interval);
					context.commit("SET_LOADING", false);
				}
			}, stepDuration);

			setTimeout(() => {
				clearInterval(interval);
				context.commit("SET_LOADING", false);
			}, totalSteps * stepDuration);
		},
	},
};
