import axios from "axios";
export default {
	namespaced: true,
	state: {
		authentication: false,
		authUser: [
			{
				id: 1,
				name: "deneme",
			},
		],
	},
	mutations: {
		setUser(state, authUser) {
			state.authUser = authUser;
			state.authentication = true;
		},
	},
	getters: {
		_user(state) {
			return state.refBrandItems;
		},
	},
	actions: {
		async getAuthentication(context, userData) {
			const apiUrl = process.env.VUE_APP_CHECK_LOGIN;
			try {
				const response = await axios.post(apiUrl, userData);
				context.commit("setUser", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
