import axios from "axios";
export default {
	namespaced: true,
	state: {
		sliderItems: [
			{
				id: 1,
				image: "slider1.webp",
				title: "Hızlı Onarım",
				text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis",
				button_text: "ÜCRETLENDİRME TABLOSU",
				button_link: "apple.com",
			},
		],
	},
	mutations: {
		setSliderItems(state, sliderItems) {
			state.sliderItems = sliderItems;
		},
	},
	getters: {
		_sliderItems(state) {
			return state.sliderItems;
		},
	},
	actions: {
		async getSliderItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_INNER_SLIDER;
			try {
				const response = await axios.get(apiUrl);
				commit("setSliderItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
