import axios from "axios";
export default {
	namespaced: true,
	state: {
		menuItems: [
			{
				id: 1,
				text: "KATEGORİLER",
				icon: "icon1.svg",
				class: "navClass",
				routeName: "loginView",
				hasSubMenu: false,
			},
			{
				id: 2,
				text: "HİZMETLER",
				icon: "icon2.svg",
				class: "navClass",
				routeName: "loginView",
				hasSubMenu: false,
			},
			{
				id: 3,
				text: "İLETİŞİM",
				icon: "icon3.svg",
				class: "navClass",
				routeName: "loginView",
				hasSubMenu: false,
			},
			{
				id: 4,
				text: "ÜCRETLENDİRME",
				icon: "icon4.svg",
				class: "navClass",
				routeName: "loginView",
				hasSubMenu: false,
			},
			{
				id: 5,
				text: "ONLİNE İŞLEMLER",
				icon: "icon5.svg",
				class: "navClass",
				routeName: "loginView",
				hasSubMenu: true,
				subMenu: [
					{ id: 1, text: "Giriş Yap / Üye Ol", routeName: "loginView" },
				],
			},
		],
	},
	mutations: {
		setMenuItems(state, menuItems) {
			state.menuItems = menuItems;
		},
	},
	getters: {
		_menuItems(state) {
			return state.menuItems;
		},
	},
	actions: {
		async getMenuItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_NAV_MENU_URL;
			try {
				const response = await axios.get(apiUrl); // axios doğrudan kullanılıyor
				commit("setMenuItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
