import axios from "axios";
export default {
	namespaced: true,
	state: {
		footerMenuItems: [
			{
				id: 1,
				text: "Kategoriler",
				link: "#",
			},
			{
				id: 2,
				text: "Hizmetler",
				link: "#",
			},
			{
				id: 2,
				text: "Online İşlemler",
				link: "#",
			},
		],
	},
	mutations: {
		setFooterMenuItems(state, footerMenuItems) {
			state.footerMenuItems = footerMenuItems;
		},
	},
	getters: {
		_footerMenuItems(state) {
			return state.footerMenuItems;
		},
	},
	actions: {
		async getFooterMenuItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_FOOTER_MENU;
			try {
				const response = await axios.get(apiUrl);
				commit("setFooterMenuItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
