<template>
	<div class="menuContainer">
		<div
			v-for="item in menuItem"
			:key="item.id"
			:class="item.class"
			@mouseenter="showSubMenu(item.id)"
			@mouseleave="hideSubMenu(item.id)">
			<div><img :src="getIconPath(item.icon)" alt="" /></div>
			<div>
				{{ item.text }}
			</div>
			<Transition name="fade">
				<div
					v-if="item.hasSubMenu && visibleSubMenus.includes(item.id)"
					class="subMenuContainer">
					<div v-if="visibleSubMenus.includes(item.id)">
						<router-link
							:to="{ name: subItem.routeName }"
							v-for="subItem in item.subMenu"
							:key="subItem.id"
							class="subMenuItem">
							{{ subItem.text }}
						</router-link>
					</div>
				</div>
			</Transition>
		</div>
	</div>
</template>
<style scoped>
	.fade-enter-to,
	.fade-leave-from {
		opacity: 1;
		transition: all 0.3s ease;
	}

	.fade-enter-from,
	.fade-leave-to {
		transform: translateY(100%);
		opacity: 0;
	}
	.subMenuItem {
		box-sizing: border-box;
		display: flex;
		position: absolute;
		background: black;
		padding: 10px;
		width: 100%;
		margin-left: -16px;
		cursor: pointer;
		color: white;
		text-decoration: none;
	}
	.subMenuContainer {
		display: flex;
		left: 16px;
		position: absolute;
		width: 100%;
		height: 100%;
		padding-top: 85px;
	}
	.menuContainer {
		font-size: 16px;
		column-gap: 2px;
		color: white;
		display: flex;
		flex-direction: row;
	}
	.navClass {
		display: flex;
		column-gap: 4px;
		padding: 8px;
		align-items: center;
		position: relative;
		cursor: pointer;
	}
	.navClass:nth-child(4) {
		margin-left: 28px;
		padding: 8px 13px;
		border: 2px solid rgba(229, 26, 74, 0.4);
		border-radius: 10px;
	}
	.navClass:nth-child(4) img {
		margin-top: -3px;
	}
	.navClass:last-child {
		padding: 8px 17px;
		flex-direction: row-reverse;
		background: rgb(229, 26, 75);
		background: linear-gradient(
			180deg,
			rgba(229, 26, 75, 1) 0%,
			rgba(71, 0, 74, 1) 100%
		);
		border-radius: 10px;
		margin-left: 25px;
	}
</style>
<script>
	import { mapGetters } from "vuex";
	export default {
		data() {
			return {
				visibleSubMenus: [],
			};
		},
		methods: {
			getIconPath(iconFileName) {
				return require(`@/assets/image/top/menuIcons/${iconFileName}`);
			},
			showSubMenu(itemId) {
				if (!this.visibleSubMenus.includes(itemId)) {
					this.visibleSubMenus.push(itemId);
				}
			},
			hideSubMenu(itemId) {
				const index = this.visibleSubMenus.indexOf(itemId);
				if (index !== -1) {
					this.visibleSubMenus.splice(index, 1);
				}
			},
		},
		created() {
			this.$store.dispatch("topMenu/getMenuItems");
		},
		computed: {
			...mapGetters({
				menuItem: "topMenu/_menuItems",
			}),
		},
	};
</script>
