import axios from "axios";
export default {
	namespaced: true,
	state: {
		withUsItems: [
			{
				id: 1,
				image: "icon1.svg",
			},
			{
				id: 2,
				image: "icon2.svg",
			},
			{
				id: 3,
				image: "icon3.svg",
			},
			{
				id: 4,
				image: "icon4.svg",
			},
			{
				id: 5,
				image: "icon5.svg",
			},
			{
				id: 6,
				image: "icon6.svg",
			},
		],
	},
	mutations: {
		setWithUsItems(state, withUsItems) {
			state.withUsItems = withUsItems;
		},
	},
	getters: {
		_withUsItems(state) {
			return state.withUsItems;
		},
	},
	actions: {
		async getWithUsItems({ commit }) {
			const apiUrl = process.env.VUE_APP_BASE_URL + process.env.VUE_APP_WITH_US;
			try {
				const response = await axios.get(apiUrl);
				commit("setWithUsItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
