import axios from "axios";
export default {
	namespaced: true,
	state: {
		categoriItems: [
			{
				id: 1,
				image: "categories1.webp",
				text: "Cep Telefonu",
			},
			{
				id: 2,
				image: "categories2.webp",
				text: "Robot Süpürge",
			},
			{
				id: 3,
				image: "categories3.webp",
				text: "Elektirikli Scooter",
			},
			{
				id: 4,
				image: "categories4.webp",
				text: "Dikey Süpürge",
			},
			{
				id: 5,
				image: "categories5.webp",
				text: "Akıllı Saat",
			},
			{
				id: 6,
				image: "categories6.webp",
				text: "Bluetooth Hoparlör",
			},
		],
	},
	mutations: {
		setCategoriItems(state, categoriItems) {
			state.categoriItems = categoriItems;
		},
	},
	getters: {
		_categoriItems(state) {
			return state.categoriItems;
		},
	},
	actions: {
		async getCategoriItems({ commit }) {
			const apiUrl =
				process.env.VUE_APP_BASE_URL + process.env.VUE_APP_CATEGORIES;
			try {
				const response = await axios.get(apiUrl);
				commit("setCategoriItems", response.data);
			} catch (error) {
				console.log(error);
			}
		},
	},
};
